@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap");
@font-face {
  font-family: "OpenSans";
  src: url(../fonts/OpenSans/OpenSans-Light.ttf) format("truetype");
  font-weight: 300;
  font-style: normal; }

@font-face {
  font-family: "OpenSans";
  src: url(../fonts/OpenSans/OpenSans-Regular.ttf) format("truetype");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "OpenSans";
  src: url(../fonts/OpenSans/OpenSans-Medium.ttf) format("truetype");
  font-weight: 500;
  font-style: normal; }

@font-face {
  font-family: "OpenSans";
  src: url(../fonts/OpenSans/OpenSans-SemiBold.ttf) format("truetype");
  font-weight: 600;
  font-style: normal; }

@font-face {
  font-family: "OpenSans";
  src: url(../fonts/OpenSans/OpenSans-Bold.ttf) format("truetype");
  font-weight: 700;
  font-style: normal; }

@font-face {
  font-family: "Poppins";
  font-weight: 500; }

/**
  Переменные для цветов
 */
*,
::after,
::before {
  box-sizing: border-box; }

html {
  font-family: "OpenSans", sans-serif;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
  scroll-behavior: smooth; }

article,
aside,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section {
  display: block; }

body {
  font-family: "OpenSans", -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
  font-size: 1rem;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
  color: #000;
  background-color: #fff; }

[tabindex='-1']:focus {
  outline: 0 !important; }

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible; }

h2 {
  font-size: 2em;
  font-weight: 600; }

p {
  margin-top: 0;
  margin-bottom: 1rem; }

abbr[data-original-title],
abbr[title] {
  text-decoration: underline;
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
  cursor: help;
  border-bottom: 0;
  -webkit-text-decoration-skip-ink: none;
  text-decoration-skip-ink: none; }

address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit; }

ul, li {
  margin: 0;
  padding: 0;
  list-style: none; }

dt {
  font-weight: 700; }

dd {
  margin-bottom: 0.5rem;
  margin-left: 0; }

blockquote {
  margin: 0 0 1rem; }

b,
strong {
  font-weight: bolder; }

small {
  font-size: 80%; }

sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline; }

sub {
  bottom: -0.25em; }

sup {
  top: -0.5em; }

a {
  color: inherit;
  text-decoration: none;
  background-color: transparent; }

a:hover {
  color: inherit;
  text-decoration: none; }

a:not([href]):not([tabindex]) {
  color: inherit;
  text-decoration: none; }

a:not([href]):not([tabindex]):focus,
a:not([href]):not([tabindex]):hover {
  color: inherit;
  text-decoration: none; }

a:not([href]):not([tabindex]):focus {
  outline: 0; }

code,
kbd,
pre,
samp {
  font-family: "OpenSans", SFMono-Regular, Menlo, Monaco, Consolas, 'Liberation Mono', 'Courier New', monospace;
  font-size: 1em; }

pre {
  margin-top: 0;
  margin-bottom: 1rem;
  overflow: auto; }

figure {
  margin: 0 0 1rem; }

img {
  vertical-align: middle;
  border-style: none; }

svg {
  overflow: hidden;
  vertical-align: middle; }

table {
  border-collapse: collapse; }

th {
  text-align: inherit; }

label {
  display: inline-block;
  margin-bottom: 0.5rem; }

button {
  border-radius: 0;
  outline: 0; }

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit; }

button,
input {
  overflow: visible; }

button,
select {
  text-transform: none; }

select {
  word-wrap: normal; }

[type='button'],
[type='reset'],
[type='submit'],
button {
  -webkit-appearance: button; }

[type='button']:not(:disabled),
[type='reset']:not(:disabled),
[type='submit']:not(:disabled),
button:not(:disabled) {
  cursor: pointer; }

[type='button']::-moz-focus-inner,
[type='reset']::-moz-focus-inner,
[type='submit']::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border-style: none; }

input[type='checkbox'],
input[type='radio'] {
  box-sizing: border-box;
  padding: 0; }

input[type='date'],
input[type='datetime-local'],
input[type='month'],
input[type='time'] {
  -webkit-appearance: listbox; }

textarea {
  overflow: auto;
  resize: vertical; }

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0; }

legend {
  display: block;
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin-bottom: 0.5rem;
  font-size: 1.5rem;
  line-height: inherit;
  color: inherit;
  white-space: normal; }

progress {
  vertical-align: baseline; }

[type='number']::-webkit-inner-spin-button,
[type='number']::-webkit-outer-spin-button {
  height: auto; }

[type='search'] {
  outline-offset: -2px;
  -webkit-appearance: none; }

[type='search']::-webkit-search-decoration {
  -webkit-appearance: none; }

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button; }

output {
  display: inline-block; }

summary {
  display: list-item;
  cursor: pointer; }

template {
  display: none; }

[hidden] {
  display: none !important; }

/* width */
::-webkit-scrollbar {
  width: 5px;
  height: 8px;
  background: #767676d7; }

/* Handle */
::-webkit-scrollbar-thumb {
  background: #000; }

.button-group .btn {
  padding: 0.5em 1em;
  font-size: 1em;
  border-radius: 8px;
  transition: 0.2s linear; }

.button-group__business-login {
  background-color: #fff;
  border: 1px solid #0D2273;
  color: #0D2273;
  font-weight: 600; }
  .button-group__business-login:hover {
    box-shadow: 0 0 0 3px #0D2273 inset; }

.button-group__sign-up {
  margin-left: 1em;
  background-color: #0D2273;
  color: #fff;
  border: 1px solid #0D2273; }
  .button-group__sign-up:hover {
    box-shadow: 0 0 0 2px white, 0 0 0 4px #0D2273; }

.header {
  position: fixed;
  width: 100%;
  box-shadow: 0 5px 6px -5px #9DA4AE; }
  .header__wrapper {
    position: relative;
    display: flex;
    align-items: center;
    padding: 1.75em 0; }
  .header .logo {
    display: flex;
    align-items: flex-end; }
    .header .logo__text {
      margin: 0 0 0 0.25em;
      font-family: "Poppins";
      font-weight: 500;
      font-size: 1.25em;
      color: #0e2478; }
  .header .nav {
    display: flex;
    margin: 0 auto; }
    .header .nav__item:not(:first-child) {
      margin-left: 1em; }
    .header .nav__link {
      padding: 0.5em 0.75em;
      font-size: 1em;
      color: #000000; }
      .header .nav__link:active {
        font-weight: 700;
        color: #0d2273; }
      .header .nav__link:hover {
        background-color: #e8effd;
        border-radius: 8px; }
    .header .nav .home {
      color: #0d2273; }

.wrapper-inner {
  max-width: 1440px;
  margin: 0 auto; }

.section-header {
  margin-top: 8em; }
